.pagination a,
.pagination span,
.pagination em {
  display: inline-block;
  min-width: 32px;
  padding: 5px 10px;
  font-style: normal;
  line-height: 20px;
  color: #333;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
  user-select: none;
  border: 1px solid transparent;
  border-radius: 3px;
  transition: border-color 0.2s cubic-bezier(0.3, 0, 0.5, 1);
}
.pagination a:hover,
.pagination a:focus,
.pagination span:hover,
.pagination span:focus,
.pagination em:hover,
.pagination em:focus {
  text-decoration: none;
  border-color: #e5e5e5;
  outline: 0;
  transition-duration: 0.1s;
}
.pagination a:active,
.pagination span:active,
.pagination em:active {
  border-color: #eee;
  transition: none;
}
.pagination .previous_page,
.pagination .next_page {
  color: #4078c0;
}
.pagination .current,
.pagination .current:hover,
.pagination [aria-current]:not([aria-current="false"]) {
  color: #fff;
  background-color: #4078c0;
  border-color: transparent;
}
.pagination .gap,
.pagination .disabled,
.pagination [aria-disabled="true"],
.pagination .gap:hover,
.pagination .disabled:hover,
.pagination [aria-disabled="true"]:hover {
  color: #999;
  cursor: default;
  border-color: transparent;
}
@supports (clip-path: polygon(50% 0, 100% 50%, 50% 100%)) {
  .pagination .previous_page::before,
  .pagination .next_page::after {
    display: inline-block;
    width: 16px;
    height: 16px;
    vertical-align: text-bottom;
    content: "";
    background-color: currentColor;
  }
  .pagination .previous_page::before {
    margin-right: 3px;
    clip-path: polygon(
      9.8px 12.8px,
      8.7px 12.8px,
      4.5px 8.5px,
      4.5px 7.5px,
      8.7px 3.2px,
      9.8px 4.3px,
      6.1px 8px,
      9.8px 11.7px,
      9.8px 12.8px
    );
  }
  .pagination .next_page::after {
    margin-left: 3px;
    clip-path: polygon(
      6.2px 3.2px,
      7.3px 3.2px,
      11.5px 7.5px,
      11.5px 8.5px,
      7.3px 12.8px,
      6.2px 11.7px,
      9.9px 8px,
      6.2px 4.3px,
      6.2px 3.2px
    );
  }
}
