  width: 24px;
  height: 24px;
  vertical-align: middle;
}
.ais-hits--item .app-result-icon {
  max-width: 100%;
  max-height: 100%;
}
.nav-search {
  transition: all 0.2s;
  background-color: #3a585f;
  border: none;
  outline: none;
  border-radius: 100px;
  font-size: 15px;
  padding: 3px 10px 3px 10px;
  margin-left: 20px;
}
.nav-search:focus {
  background-color: white;
  color: #2b2e3b;
}
.nav-search:focus::-webkit-input-placeholder {
  color: white;
}
.nav-search:focus::-moz-input-placeholder {
  color: white;
}
#search-hint {
  display: inline;
  position: relative;
  right: 7%;
  top: 4.76px;
  transition: all 0.2s;
}
@media (max-width: 691px) {
  #search-hint {
    right: 10%;
  }
}
#search-hint-dialog {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: absolute;
  z-index: 1;
  width: 350px;
  top: 71px;
  right: -63%;
  border: none;
  border-radius: 10px;
  box-shadow: #0003 0.2em 0.2em 0.2em;
  background-color: white;
}
@media (min-width: 693px) and (max-width: 1163px) {
  #search-hint-dialog {
    top: 103px;
    right: -48%;
  }
}
@media (max-width: 690px) {
  #search-hint-dialog {
    top: 148px;
    right: -1%;
  }
}
#search-hint-dialog::before {
  content: "";
  position: absolute;
  right: 88%;
  top: -13px;
  width: 0;
  height: 0;
  border-right: 17px solid transparent;
  border-left: 17px solid transparent;
  border-bottom: 26px solid white;
}
@media (max-width: 1230px) {
  #search-hint-dialog::before {
    right: 76%;
  }
}
@media (min-width: 693px) and (max-width: 1163px) {
  #search-hint-dialog::before {
    right: 68%;
  }
}
@media (max-width: 690px) {
  #search-hint-dialog::before {
    right: 85%;
  }
}
.dialog-button {
  position: relative;
  top: 30px;
  height: 30px;
  border-radius: 5px;
}
.dialog-text {
  text-align: left;
}
#hits {
  position: absolute;
  display: none;
  flex-direction: column;
  justify-self: center;
  width: 500px;
  max-height: 100%;
  overflow-y: auto;
  box-shadow: 0 6px 35px 0 rgba(0, 0, 0, 0.35);
  border-radius: 4px;
  background-color: white;
  margin-left: 49.5%;
  top: 80px;
}
@media (max-width: 992px) {
  #hits {
    width: 100%;
    margin-left: 0;
    margin-top: 10.5%;
  }
}
@media (min-width: 993px) and (max-width: 1270px) {
  #hits {
    margin-top: 6%;
  }
}
.first {
  order: -1;
}
.hits-container:empty {
  display: none;
}
.hits-container::before {
  content: attr(title);
  display: block;
  margin: 0;
  padding: 5px 20px;
  font-size: 10pt;
  color: #7c9398;
  background-color: #74b1be7a;
}
.ais-hits--item {
  background-color: white;
  padding: 10px 20px;
  border-bottom: 1px solid #ddd;
  margin: 0;
  color: #7e7e7e;
}
.ais-hits--item:hover,
.ais-hits--item.active {
  background-color: #f0f0f0;
}
.ais-hits.ais-hits__empty {
  display: none;
}
.ais-hits--item em {
  text-decoration-line: underline;
}
.ais-search-box--reset {
  display: none;
}
.ais-search-box--magnifier {
  display: none;
}
.ais-search-box--reset-wrapper {
  display: inline !important;
  position: relative;
  right: 30px;
  top: 6px;
}
::-webkit-input-placeholder {
  color: #74b1be;
}
::-moz-input-placeholder {
  color: #74b1be;
}
.search-small-text {
  font-size: 10pt;
}
.electron-version-container figure {
  margin: 0 !important;
}
.electron-version-container .electron-version {
  padding: 12px;
}
.electron-version-container .electron-version pre {
  max-width: calc(100vw - 50px);
}
.electron-version-container .electron-version pre code span.no-select {
  user-select: none;
}
@media (min-width: 768px) {
  .electron-version-container {
    display: flex;
  }
  .electron-version-container .electron-version {
    flex: 1;
    overflow: hidden;
    transition: opacity 0.4s ease-in-out, flex 0.4s ease-in-out;
  }
  .electron-version-container .electron-version.expanded-version {
    flex: 2;
  }
  .electron-version-container .electron-version.shrunk-version {
    opacity: 0.6;
  }
}
.kb-shortcut {
  display: none;
  position: fixed;
  z-index: 5;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}
.kb-shortcut-content {
  background-color: #fefefe;
  margin: 150px auto;
  border: 1px solid #888;
  min-width: 800px;
  width: 450px;
}
.kb-shortcut-header {
  padding: 2px 16px;
  color: #7c9398;
  background-color: #74b1be7a;
}
.kb-shortcut-header .close {
  margin-top: 20px;
  margin-bottom: 20px;
  float: right;
  font-size: 28px;
  font-weight: bold;
}
.kb-shortcut-header .close:hover {
  color: #097e95;
  cursor: pointer;
}
.kb-shortcut-body {
  padding: 20px;
}
.kb-shortcut-body table {
  width: 100%;
}
.kb-shortcut-footer {
  padding: 2px 16px;
  background-color: #5cb85c;
  color: white;
}
.kb-shortcut th,
.kb-shortcut td {
  padding: 5px;
  text-align: left;
}
.kb-shortcut h2 {
  margin: 0.5em 0;
}
.governance .d-flex {
  display: flex;
}
.governance .d-md-flex {
  display: flex;
}
.governance .flex-column {
  flex-direction: column;
}
.governance .flex-md-row {
  flex-direction: row;
}
